
.container{
   width: 100%;
   height: 100%;
}

.containerFluid :first-child{
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-rows: auto auto;
    text-align: center;
    color: white;
}

.containerFluid :first-child h1{
    margin: auto;
}

.containerFluid :first-child button{
    margin-left: auto;
    margin-right: 30px;
}